import { Injectable, Inject } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Router } from '@angular/router';
import { APIService } from './api.service';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Global_Variables } from '../app/properties';
import { jwtDecode } from "jwt-decode";

const STORAGE_KEY = 'token';

const bDebug = true;
@Injectable({
  providedIn: 'root'
})
export class AuthService {


  private oAPIService: APIService|null = null;

  public isAuthenticated = new BehaviorSubject<boolean>(false);

  constructor(@Inject(APIService) private oAPI:any, @Inject(LOCAL_STORAGE) private oStorage: StorageService, private router: Router) {
    this.oAPIService = oAPI;
  }

  async checkAuthenticated() {
    if (bDebug)
      console.info("checkAuthenticated");
    const authenticated = await this.exists();
    this.isAuthenticated.next(authenticated);
    console.info(authenticated);
    return authenticated;
  }



  async login(data: any) {
    try {
      console.log(data);
      const id = await this.oAPIService?.performLogin(data);
      Global_Variables.token = id;
      if (id) {
        let o:any = jwtDecode(id.access_token)
        console.log(o)
        let jwt_user_fields = JSON.parse(o['user_fields']) ;
        console.log(jwt_user_fields)
        Global_Variables.sEntityID = jwt_user_fields["ENTITYID"];
        Global_Variables.iUserID = jwt_user_fields["UID"];
        Global_Variables.sUserType = jwt_user_fields["TYPE"];
        Global_Variables.token = id;
        this.oStorage.set(STORAGE_KEY, id);
        this.isAuthenticated.next(true);
        await this.router.navigate(['qdisplay']);
        return;
      }
    }
    catch (err:any) {
      console.log(err);
      // Global_Variables.token = "SkipValidation"
      // this.isAuthenticated.next(true);
      // await this.router.navigate(['qdisplay']);
      // return ;
      throw Error(JSON.stringify(err.error));
    }
    // console.log("Not Authenticated") ;
    // throw Error('Not Authenticated ');
  }
  

  async logout(redirect: string) {
    try {
      this.oStorage.remove(STORAGE_KEY);
      Global_Variables.token = undefined;
      this.isAuthenticated.next(false);
      this.router.navigate([redirect]);
    } catch (err) {
      console.error(err);
    }
  }
  exists() {
    var id = this.oStorage.get(STORAGE_KEY);
    if (id) {
      try {
        // let o = this.jwtHelper.decodeToken(id.id_token);
        // if (bDebug)
        //   console.info(o);
        let o:any = jwtDecode(id.access_token)
        let jwt_user_fields = JSON.parse(o['user_fields']) ;
        console.log(jwt_user_fields)
        Global_Variables.sEntityID = jwt_user_fields["ENTITYID"];
        Global_Variables.iUserID = jwt_user_fields["UID"];
        Global_Variables.sUserType = jwt_user_fields["TYPE"];
        return true;
      }
      catch (err) {
        console.error(err);
        return false;
      }
    }
    return false
  }
}
