import { Component } from '@angular/core';

import { ActivatedRoute, Router } from '@angular/router';
import { TitleBarService } from './title-bar.service';
import { SwUpdate } from '@angular/service-worker';
import { PrimeNGConfig } from 'primeng/api';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  sTitle = 'Q# Display';
  sMqttStatus = '';
  constructor(private oTBS: TitleBarService,private primengConfig: PrimeNGConfig,
    private router: Router, private swUpdate: SwUpdate
  ) {
    this.router.navigate(['login']);
  }

  ngOnInit() {
    if (this.swUpdate.isEnabled) {
      this.swUpdate.available.subscribe(() => {
        console.log("New Version Available.");
        if (confirm("New version available. Load it ?")) {
          window.location.reload();
        }
      })
    }
    // this.oTBS.oCurrentTitle.subscribe(obj => {
    //   setTimeout(() => {
    //     this.sTitle = obj.title;
    //     this.sMqttStatus = obj.status;
    //     console.log(this.sMqttStatus)
    //   }, 1);
    // });
  }
}

